import { useState } from 'react'
import { db } from '../firebase'
import { collection, addDoc } from 'firebase/firestore'
import { useNavigate } from 'react-router-dom'
import MensagemErro from './MensagemErro'

function AddEvent()
{
    const [nome,setNome] = useState("")
    const [descricao,setDescricao] = useState("")
    const [meta,setMeta] = useState("")
    const navegar = useNavigate()
    const [carregando,setCarregando] = useState(false)
    const [sucesso,setSucesso] = useState(false)

    async function handleSubmit(e)
    {
        e.preventDefault()

        setCarregando(true)
        try {
            await addDoc(collection(db, "eventos"), {
                nome,
                descricao,
                meta: Number(meta),
                valor_arrecadado: 0,
            })

            setNome("")
            setDescricao("")
            setMeta("")

            setSucesso(true)

            setTimeout(() => {
                setSucesso(false)
                navegar('/')
            }, 3000)
            
        } catch (error) {
            console.log("Erro ao adicionar evento ", error)
        } finally {
            setCarregando(false)
        }
    }

    return (
        <div>
            <button onClick = {() => navegar('/')} style={{margin: "0.5em 0"}}>voltar</button>

            { carregando ? ( <div style = {{height: 100+"%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>A criar evento...</div>)
            : 
            (sucesso ? <MensagemErro mensagem = "Evento Criado com sucesso!" tipoMensagem = "sucesso" /> : 
                (<form onSubmit = {handleSubmit} style = {{display: "flex", flexDirection: "column", gap: 0.5+"em", justifyContent: "center", alignItems: "center"}}>
                    <h2>Adicionar Evento de Doação</h2>
    
                    <input 
                        type = "text"
                        name = "nome"
                        value = {nome}
                        placeholder = "Nome do evento"
                        onChange = {(e) => setNome(e.target.value)}
                        required
                    />
    
                    <input 
                        type = "text"
                        name = "descricao"
                        value = { descricao }
                        placeholder = "Descricao do evento"
                        onChange = { (e) => setDescricao(e.target.value)}
                        required
                    />
    
                    <input 
                        type = "number"
                        name = "meta"
                        value = { meta }
                        placeholder = "Meta de arrecadação (KZ)"
                        onChange = { (e) => setMeta(e.target.value) }
                    />
    
                    <button type = "submit">Criar evento</button>
                </form>)
            )}
        </div>
    )
}

export default AddEvent